.card{
    overflow:hidden ;
    box-shadow: 0px 5px 10px 0px;
    color : rgb(192, 176, 177);
    width: 150px;
    height: 160px;
    border-radius: 2px;
    border: 2px solid;
}
.card:hover{
   -webkit-transform: scale(1.1);
   transform: scale(1.1);
}
 
img{
    width: 100%;
    height: 100%;
}